<template>
    <div>
    <v-layout row>
        <v-card color="blue-grey" dark tile flat>
            <v-card-text>{{ info }}</v-card-text>
        </v-card>
    </v-layout>
    <v-layout row>
        <v-flex xs12 sm12>
            <v-card>
                <v-list two-line subheader>
                    <v-list-tile v-for="item of items" avatar :key="item.name + Math.random(99999)">
                        <v-list-tile-avatar>
                            <img :src="item.img">
                        </v-list-tile-avatar>

                        <v-list-tile-content>
                            <v-list-tile-title>{{ item.name }}</v-list-tile-title>
                            <v-list-tile-sub-title>{{ item.type }}</v-list-tile-sub-title>
                        </v-list-tile-content>


                        <v-list-tile-action >
                            <v-btn @click="downloadItem(item)"  icon ripple>
                                <v-icon color="green lighten-1">save_alt</v-icon>
                            </v-btn>
                        </v-list-tile-action>

                        <v-list-tile-action >
                            <v-btn @click="deleteItem(item)" icon ripple>
                                <v-icon color="red lighten-1">delete</v-icon>
                            </v-btn>
                        </v-list-tile-action>
                        
                    </v-list-tile>
                </v-list>
            </v-card>
        </v-flex>
  </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: "Esta es la cola de descargas, podrs descargar todos los vdeos / Audios agregados, descargar individual o eliminar cualquier tem."
    };
  },
  computed: {
    items() {
      return this.$store.getters.gettailDownload;
    }
  },
  methods:{
      deleteItem(item){
          this.$store.dispatch('deleteToTailDownload', item)
      },
      downloadItem(item){
          this.$store.dispatch('downloadItem', item)
      }
  }
};
</script>




